<template>
  <div class="my-table">
    <el-table :data="data" row-key='data'>
      <my-column v-for="(item,index) in col" :key="index" :col="item"></my-column>
    </el-table>
  </div>
</template>

<script>
import MyColumn from './myColum'
export default {
  components: {
    MyColumn
  },
  props: {
    col: {
      type: Array
    },
    data: {
      type: Array
    }
  }
}
</script>
<style scoped>
 /deep/.el-table .cell{
    line-height: 60px !important;
}
</style>


